import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams} from 'ionic-angular';
import { DirectorProvider } from '../../providers/director/director';
import { GeocoderProvider } from '../../providers/geocoder/geocoder';
import { ProfileProvider } from '../../providers/profile/profile';
/**
 * Generated class for the RestaurantDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-food-details',
  templateUrl: 'food-details.html',
})
export class FoodDetailsPage {



  public resImage: any;
  public rate: number = 4;
  public details: string = "menus";
  public location_Name;
  public items: any;
  public categories: any;

  constructor(public gCode: GeocoderProvider, public ph: ProfileProvider, public director: DirectorProvider, public navCtrl: NavController, public navParams: NavParams) {
    this.resImage =  director.restuarant_Detail.image
  }


  ionViewDidLoad(){
    this.ph.getResturantProfile().child(this.director.restuarant_Detail.key).child('category').child(this.director.food_Detail.key).child('foods').on('value', userProfileSnapshot => {
      this.items = [];
       console.log(userProfileSnapshot.val())
      //  this.pop.hideLoader()
      userProfileSnapshot.forEach( snap => {
        
          
          this.items.push({
            key: snap.key,
            image: snap.val().pic,
            name: snap.val().name,
            price: snap.val().price
          });

         
          console.log(this.items)
          return false
      
          
        
        });
    })
  }
  

  goDetails(item){
    this.navCtrl.push('CurrentfoodDetailsPage')
    this.director.currentfood_Detail = item
    console.log(item);
  }

  Accept(){
    this.navCtrl.push('BasketPage')
  }


}



